import React from "react";
import { Form, Input, Button, message } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { selectItemById, updateProduct } from "../../../store/slices/productsSlice";
import { AppDispatch, RootState } from "../../../store"; //  RootState
import { Product } from "../../../types/products";
import { useNavigate } from "react-router";

import "./style.scss";

const ProductEdit = () => {
    const dispatch = useDispatch<AppDispatch>();
    const navigate = useNavigate();
    const productId = useSelector((state: RootState) => state.product.productId); //   RootState
    const product = useSelector((state: RootState) => selectItemById(state, productId));
    const [form] = Form.useForm();

    const handleUpdateProduct = (values: Product) => {
        const updatedProduct = { ...values, id: product?.id }; //  id
        dispatch(updateProduct(updatedProduct));
        message.success('  !');
        navigate('/products');
    };

    return (
        <div className="product-edit">
            <Form
                form={form}
                layout="vertical"
                onFinish={handleUpdateProduct}
                initialValues={{ 
                    title: product?.title, 
                    description: product?.description, 
                    price: product?.price, 
                    image: product?.image 
                }}
                className="product-edit__form"
            >
                <h1 className="product-edit__form__title"> </h1>
                <Form.Item
                    name="title"
                    label=" "
                    rules={[{ required: true, message: '  ' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="description"
                    label=" "
                    rules={[{ required: true, message: '  ' }]}
                >
                    <Input.TextArea rows={4} />
                </Form.Item>
                <div className="product-edit__form__additional-fields">
                    <Form.Item
                        name="price"
                        label=""
                        rules={[{ required: true, message: ' ' }]}
                        className="product-edit__form__additional-fields__field"
                    >
                        <Input type="number" />
                    </Form.Item>
                    <Form.Item
                        name="image"
                        label="  (URL)"
                        rules={[{ required: true, message: '    ' }]}
                        className="product-edit__form__additional-fields__field"
                    > 
                        <Input type="url" />
                    </Form.Item>
                </div>
                <Form.Item className="product-edit__form__actions">
                    <Button type="primary" onClick={() => window.history.back()}>{`<<`} </Button>
                    <Button type="primary" htmlType="submit" style={{ marginLeft: '30px' }}>
                         
                    </Button>
                </Form.Item>
            </Form>
        </div>
    );
};

export default ProductEdit;
